import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as AppActions from './../../actions/app';
import * as GameActions from './../../actions/game';
import * as AuthActions from './../../actions/auth';
import Home from './../../components/Home';
import Intro from './../../components/HomeIntro';
import Genres from './../../components/HomeGenres';

class HomeContainer extends Component {
  constructor() {
    super();
    this._selectGenre = this._selectGenre.bind(this);
  }

  componentDidMount() {
    this.props.appActions.reset();
    this.props.gameActions.reset();
  }

  render() {
    const { genres } = this.props.app;
    const genreList = [];
    Object.keys(genres).map(key => {
      genreList.push(genres[key]);
    });


    return (
      <Home>
        <Intro />
        <Genres list={genreList} onSelect={this._selectGenre}/>
      </Home>
    );
  }

  _selectGenre(index) {
    this.props.appActions.navigateToGame(index);
  }
}

function mapStateToProps(state) {
  return {
    app: state.app
  };
}

function mapDispatchToProps(dispatch) {
  return {
    appActions: bindActionCreators(AppActions, dispatch),
    gameActions: bindActionCreators(GameActions, dispatch),
    authActions: bindActionCreators(AuthActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(HomeContainer);



