import React, { Component } from 'react';

// require('./_index.scss');

const ReadyToPlay = ({ onClick })=> {
  return (
    <div className="ReadyToPlay" onClick={onClick}>
      <div className="ready-fix">
        <div className="waiting"></div>
        <div className="text-wrapper">Ready to play?</div>
        <div className="move-text-wrapper">Tap to start!</div>
      </div>
    </div>
  );
};

export default ReadyToPlay;



