import { SHARE } from './constants';

export default class Facebook {
  constructor() {
    this.accessToken = '';
    this.expiresIn = '';
    this.userID = '';
  }

  login() {
    return new Promise((resolve, reject) => {
      FB.login(response => {
        if (response.authResponse) {
          this.accessToken = response.authResponse.accessToken;
          this.expiresIn = response.authResponse.expiresIn;
          this.userID = response.authResponse.userID;
          resolve(response);
        } else {
          reject({});
        }
      }, { scope: 'public_profile, email, user_friends, publish_actions' });
    });
  }

  logout() {
    return FB.logout(response => {
      return response;
    });
  }

  getFriends() {
    return new Promise(resolve => {
      FB.api('/me/friends', response => {
        resolve(response);
      });
    });
  }

  postScore(score, genre) {
    return new Promise(resolve => {
      FB.ui({
        method: 'feed',
        link: 'https://imt.rocks',
        app_id: '1104136212951513',
        caption: 'Your musical IQ - get it at IMT.rock and challenge your friends.',
        picture: SHARE[genre].picture,
        name: SHARE[genre].name(score),
        description: `Ive just scored ${score} pts playing Infinite Musica Trivia. Do you think
                      you can beat my score? Prove it now at IMT.rock`,
        scope: 'popup'
      }, (response) => {
        resolve(response);
      });
    });
  }
}



