import { hashHistory as history } from 'react-router';
import * as appActions from './app';
import Game from './../core/Game';

const updateScore = pts=> ({ type: 'UPDATE_SCORE', pts });
const setHelp = typeLvl=> ({ type: typeLvl });
const setOptions = options=> ({ type: 'SET_OPTIONS', options });
const setCurrentTrack = track=> ({ type: 'SET_CURRENT_TRACK', track });
const storePlayedTrack = track=> ({ type: 'UPDATE_PLAYED_TRACKS', track });
const resetHelp = ()=> setHelp('RESET_HELP');
const resetGame = ()=> ({ type: 'RESET_GAME' });

export function navigateToHome() {
  history.replace('/');
}

export function lose() {
  ga('send', 'event', 'event', 'lost-game', 'true');
  history.replace('/finish');
  return {
    type: ''
  };
}

export function reset() {
  return (dispatch)=> {
    dispatch(resetGame());
  };
}

export function restart() {
  return (dispatch)=> {
    dispatch(reset());
    dispatch(appActions.reset());
    navigateToHome();
  };
}

export function setTracks(tracks) {
  return {
    type: 'SET_TRACKS',
    tracks
  };
}

export function getReadyToPlay() {
  return (dispatch, getState)=> {
    const { track, options, tracksLeft } = Game.play(getState().game);
    dispatch(setCurrentTrack(track));
    dispatch(setTracks(tracksLeft));
    dispatch(setOptions(options));
    dispatch(resetHelp());
  };
}

export function win() {
  return (dispatch, getState)=> {
    const score = Game.getScore(getState().game);
    dispatch(updateScore(score));
    dispatch(storePlayedTrack(getState().game.currentTrivia.currentTrack.original));
    dispatch(appActions.showWin());
    // loads and get ready for next song
    dispatch(getReadyToPlay());
  };
}

export function updateTime(time) {
  return {
    type: 'UPDATE_TIME',
    time
  };
}

export function timerTick(time) {
  return (dispatch, getState)=> {
    dispatch(updateTime(time));
    const lvl = Game.evaluateHelpLevel(getState().game);
    // LVL0: no help
    // LVL1: show cover
    // LVL2: show album name
    // LVL3: show artist name
    // LVL4: remove 3 options
    dispatch(setHelp(`HELP_${lvl}`));
  };
}



