import { hashHistory as history } from 'react-router';
import * as gameActions from './game';
import Game from './../core/Game';

const setLoadingStatus = (status = false)=> {
  return {
    type: 'LOADING',
    value: status
  };
};

export function startSearching() {
  return setLoadingStatus(true);
}

export function stopSearching() {
  return setLoadingStatus(false);
}

const resetApp = ()=> ({ type: 'RESET_APP' });

export function navigateToGame(genreIndex) {
  history.replace(`game/${genreIndex}`);
}

export function genreSelection(genre) {
  return {
    type: 'SELECT_GENRE',
    genre
  };
}

export function switchWin(value) {
  return {
    type: 'SWITCH_WIN',
    value
  };
}

export function playAgain() {
  ga('send', 'event', 'event', 'play-againg', 'true');
  return {
    type: 'NEW_ROUND'
  };
}

export function showWin() {
  return switchWin(true);
}

export function hideWin() {
  return switchWin(false);
}

export function hidePopup() {
  return switchWin(false);
}

export function reset() {
  return (dispatch)=> {
    dispatch(resetApp());
  };
}

export function selectGenre(genreIndex) {
  return (dispatch, getState) => {
    ga('send', 'event', 'event', 'new-game', genreIndex);
    dispatch(startSearching());
    dispatch(genreSelection(genreIndex));
    // use getState() to get updated store after genre update
    Game.start(getState().app).then((tracks)=> {
      dispatch(stopSearching());
      dispatch(gameActions.setTracks(tracks));
      // force play until user real player component is ready
      dispatch(gameActions.getReadyToPlay());
    });
  };
}



