import React, { Component } from 'react';

// require('./_index.scss');

const Results = ({ score, songName, onContinue, onLeaderboard, onShare, onLogin, genre, loginStatus }) => (
  <div className="Results wrapper-container" >
    <div className="results-fix">
      <div className="image"></div>
      <h1 className="text-wrapper">We can work it out</h1>
      <div className="correct-answer">Correct song name was <b>{songName}</b></div>
      <div className="score">
        {score > 0 ? '' : ''}Your final score is <span className="figure">{score} pts.</span>
      </div>
      <button className="play-again-cta" onClick={onContinue}>I can do this better!</button>

      { loginStatus ?
        <div className="social-buttons">
          <a href={`/#/leaderboard/${genre}`} className="leaderboard-btn">View leaderboard</a>
          <div className="share-btn" onClick={() => {onShare(score, genre)}}>Share</div>
        </div>
        :
        <div className="social-buttons">
          <div className="leaderboard-btn" onClick={() => {onLogin({score, genre}, false, genre)}}>View leaderboard</div>
          <div className="share-btn" onClick={() => {onLogin({score, genre}, true)}}>Share</div>
          <div className="login-btn" onClick={() => {onLogin({score, genre})}}>Login</div>
        </div>
      }

    </div>
  </div>
);

export default Results;



